import { createSlice } from "@reduxjs/toolkit";
import { addShippingAddress } from "./cartSlice";
  const initialState = {
    loading : true,
    error : false,
    order : {},
    errorMsg : null,
    payLoading : false,
    payErrMsg: null,
    paySuccess : false,
    payError : false,
  }

  const orderDetailSlice = createSlice({
    name : 'orderDetail',
    initialState : initialState,
    reducers : {
        orderDetailSuccess : (state,action) => {
            return {
                loading:false,
                error:false,
                order : action.payload

            }
        },
        orderDetailLoading : (state,action) => {
            state.loading = true
        },
        orderDetailError : (state,action) => {
            state.error = true
            state.errorMsg = action.payload.detail
        },
        payOrderLoading : (state,action) => {
            state.payLoading = true
        },
        payOrderSuccess : (state,action) => {
            state.payLoading = false
            state.paySuccess = true
        },
        payOrderError : (state,action) => {
            state.payError = true
            state.payErrMsg = action.payload
        },
        payOrderReset : (state,action) => {
            state.payLoading = false
            state.payErrMsg=  null
            state.paySuccess =  false
            state.payError =  false
        }

    }
  })


  export const {orderDetailSuccess,orderDetailError,orderDetailLoading,payOrderLoading,payOrderSuccess,payOrderError,payOrderReset} = orderDetailSlice.actions
  export default orderDetailSlice.reducer