import React from 'react';
import { useEffect } from 'react';
import { Row, Col } from 'react-bootstrap';
import Product from '../components/Product';
import { useDispatch, useSelector } from 'react-redux';
import { productList, productListError } from '../features/productSlice';
import Loader from '../components/Loader';
import Message from '../components/Message';
import axios from 'axios';
import Paginate from '../components/Paginate';
import { useSearchParams } from 'react-router-dom';
import Carousal from '../components/Carousal';
import { BASE_URL } from '../constants';

function HomeScreen() {
  const dispatch = useDispatch();
  const products = useSelector((state) => state.product.products);
  const loading = useSelector((state) => state.product.isLoading);
  const error = useSelector((state) => state.product.error);
  const page = useSelector((state) => state.product.page)
  const pages =  useSelector((state) => state.product.pages)
  const [searchparams] = useSearchParams()
  const searchquery = searchparams.has('searchquery') ? searchparams.get('searchquery') : ""
  const currentpage = searchparams.has('page') ? searchparams.get('page') : ""


  useEffect(() => {
    async function fetchProducts() {
      try {
        dispatch(productList({ products: [], isLoading: true }));
        const response = await axios.get(`${BASE_URL}getProducts/?searchquery=${searchquery}&page=${currentpage}`)
        if (response) {
          console.log('responsein api',response)
          dispatch(productList({ products: response.data.products,
             isLoading: false ,
             page : response?.data?.page,
             pages : response?.data?.num_of_pages
            }));
        }
      } catch (error) {
        console.log(error);
        dispatch(productListError({ error: error, isLoading: false }));
      }
    }
    fetchProducts();
  }, [searchquery,currentpage]);


  return (
    <div>
      {!searchquery && (<Carousal/>)}
      <h1>Latest Products</h1>
      {loading ? (
        <Loader />
      ) : error ? (
        <Message variant='danger'>{error.message}</Message>
      ) : (
        <div>
          <Row>
          {products.map((product) => (
            <Col key={product._id} sm={12} md={6} lg={4} xl={3}>
              <Product product={product} />
            </Col>
          ))}
        </Row>
        <div className='d-flex justify-content-center align-items-center w-100'>
          <Paginate page={page} pages={pages} searchquery={searchquery} />
        </div>
       
        </div>
        
      )}
    </div>
  );
}

export default HomeScreen;
