import {createSlice} from '@reduxjs/toolkit';

const initialState = {
    isLoading : false,
    userDetail : null,
    isError : null,
    orderLoading : false,
    userOrders : [],
    orderError : null
}

const userDetailSlice = createSlice({
    name : 'userDetail',
    initialState : initialState,
    reducers : {
        userDetailLoading  : (state,action) => {
            state.isLoading = true
            state.userDetail = null
            state.isError = null
        },
        userDetailSuccess : (state,action) => {
            state.isLoading = false
            state.userDetail = action.payload
            state.isError = null
        },
        userDetailError : (state,action) => {
            state.isLoading = false
            state.userDetail = null
            state.isError = action.payload.detail
        },
        userOrdersLoading : (state,action) => {
            state.orderLoading = true
        },
        userOrdersSuccess : (state,action) => {
            state.orderLoading = false
            state.userOrders = action.payload
            state.orderError = null
        },
        userOrdersFailure : (state,action) => {
            state.orderLoading = false
            state.userOrders = null
            state.orderError = action.payload
        },
        userDetailReset : (state,action) => initialState
    }
})

export const {
    orderDetailLoading,
    orderDetailSuccess,
    orderDetailError,
    userOrdersLoading,
    userOrdersSuccess,
    userOrdersFailure,
    orderDetailReset
} = userDetailSlice.actions;
export default userDetailSlice.reducer