import React,{useEffect,useState} from 'react'
// import { Link,useNavigate, useLocation } from 'react-router-dom';
import { Button, Table,Row,Col } from 'react-bootstrap'
import Loader from '../components/Loader';
import Message from '../components/Message';
import { useDispatch, useSelector } from 'react-redux';
import { LinkContainer } from 'react-router-bootstrap';
import { productListLoading,productListSuccess,productListError } from '../features/productListSlice';
import {  useNavigate } from 'react-router-dom';

function ProductListScreen() {

    const dispatch = useDispatch()
    const navigate = useNavigate()
    const userInfo = useSelector((state)=>state.user.userInfo)
    const productList = useSelector((state)=>state.productList)
    const [loadingdata,setLoadingData] = useState(false)
    const [errorData,setErrorData] = useState(false)


    const {loading,products,error,errMsg} = productList

    const getProductList = async() => {
        dispatch(productListLoading())
        try{
            const headers = new Headers({
                'Content-Type' : 'application/json',
                Authorization : `Bearer ${userInfo?.token}`
            })
            const response = await fetch('http://127.0.0.1:8000/apis/admin/products/',{
                method : 'GET',
                headers
            })
            if(!response.ok){
                const error = await response.json()
                return new Error(error)
            }
            
            const data = await response.json()
            if(data?.products.length === 0){
                return new Error(data?.message)
            }
            dispatch(productListSuccess(data?.products))
        }
        catch(error){
            console.log(error)
            dispatch(productListError(error))
        }
    }

    const deleteHandler = async(id) => {
      
        
        try{
            const headers = new Headers({
                'Content-Type' : 'application/json',
                Authorization : `Bearer ${userInfo.token}`
            })
            const response = await fetch(`http://127.0.0.1:8000/apis/admin/product/${id}`,{
                method : 'DELETE',
                headers
            })
            if(!response.ok){
                const error = await response.json()
                return new Error(error.message)
            }
            const data = await response.json()
            getProductList()
        }
        catch(error){
            console.log(error)
        }
    }

    useEffect(() => {
        if(userInfo && userInfo.isAdmin){
            getProductList()
        }else{
            navigate('/')
        }
    },[userInfo])

    const createProductHandler = async() => {
        try{
            const headers = new Headers()
            headers.append('Content-Type','application/json')
            headers.append('Authorization',`Bearer ${userInfo?.token}`)

            const response = await fetch(`http://127.0.0.1:8000/apis/admin/createProduct/`,{
                method:'POST',
                headers : headers,
            })

            if(!response.ok){
                throw new Error('Error occurred while creating product')
            }
            const data = await response.json()
            getProductList()
            setLoadingData(false)
            setErrorData(false)
        }catch(error){
            console.log('error',error.message)
            setErrorData(true)

        }
    }


  return (
    <div>
        <Row className='align-item-center'>
            <Col >
                 <h1>Products</h1>
            </Col>
            <Col className='text-end'>
                <Button className='my-3' onClick={createProductHandler}>
                   <i className='fas fa-plus'></i> Create Product
                </Button>
            </Col>
        </Row>
        {loading || loadingdata ? (<Loader/>) : 
                error || errorData ? (<Message variant='danger'>{errMsg!=="" ? errMsg : errorData ? "Error occurred while creating product" : "Some Error occurred"}</Message>)
                    : (
                        <Table striped responsive bordered hover className='table-sm'>
                            <thead>
                                <th>ID</th>
                                <th>NAME</th>
                                <th>PRICE</th>
                                <th>CATEGORY</th>
                                <th>BRAND</th>
                                <th></th>
                            </thead>
                            <tbody>
                                {products.map((product)=> (
                                    <tr key={product._id}>
                                        <td >{product._id}</td>
                                        <td >{product.name}</td>
                                        <td >${product.price}</td>
                                        <td >{product.category}</td>
                                       
                                        <td >
                                            <LinkContainer to={`/admin/product/${product._id}`}>
                                                <Button variant='light'>
                                                    <i className='fas fa-edit'></i>
                                                </Button>
                                            </LinkContainer>
                                            <Button variant='danger' onClick={() => deleteHandler(product._id)}>
                                            <i className='fas fa-trash'></i>
                                            </Button>
                                        </td>
                                        
                                    </tr>
                                ))}
                                
                            </tbody>

                        </Table>
                    )}
      
    </div>
  )
}

export default ProductListScreen
