import { createSlice } from "@reduxjs/toolkit";

const initialState = {
    isLoading: false,
    userInfo: null,
    isError: null
};

const userSlice = createSlice({
    name: 'user',
    initialState: initialState,
    reducers: {
        loginUserBefore: (state, action) => {
            state.isLoading = true;
            state.userInfo = null;
            state.isError = null;
        },
        loginUser: (state, action) => {
            state.isLoading = false;
            state.userInfo = action.payload;
            state.isError = null;
        },
        loginUserError: (state, action) => {
            state.isLoading = false;
            state.userInfo = null;
            state.isError = action.payload.detail;
        },
        logoutUser: (state, action) => {
            state.isLoading = false;
            state.userInfo = null;
            state.isError = null;
        },
        registerUser : (state, action) => {
            state.isLoading = false;
            state.userInfo = action.payload.data;
            state.isError = null;
        },
        registerUserError : (state, action) => {
            state.isLoading = false;
            state.userInfo = null;
            state.isError = action.payload.detail;
        },
    }
});

export const { loginUserBefore, loginUser, loginUserError, logoutUser,registerUser, registerUserError } = userSlice.actions;

export default userSlice.reducer;
