import React,{useState} from 'react'
import { useNavigate} from 'react-router-dom';
import { addShippingAddress } from '../features/cartSlice';
import { useDispatch, useSelector } from 'react-redux';
import { Button,Form, FormControl,Col } from 'react-bootstrap'
import FormContainer from '../components/FormContainer';
import CheckoutSteps from '../components/CheckoutSteps';
import { addPaymentMethod } from '../features/cartSlice';

function PaymentScreen() {

    const cart = useSelector((state)=>state.cart)
    const {shippingAddress} = cart
    const [paymentMethod,setPaymentMethod] = useState('')
    const navigate = useNavigate()
    const dispatch = useDispatch()

    if(!shippingAddress || shippingAddress === '' || shippingAddress === null){
        navigate('/shipping')
    }
    const submitHandler = (e) => {
        e.preventDefault()
        dispatch(addPaymentMethod({
            paymentMethod : paymentMethod
        }))
        navigate('/placeorder')
    }

  return (
    <div>
        <FormContainer>
            <CheckoutSteps step1 step2 step3/>
            <Form onSubmit={submitHandler}>
                <Form.Group>
                    <Form.Label as='legend' className='mb-3'>Select Method</Form.Label>
                    <Col>
                    <Form.Check 
                    className='mb-3'
                    type='radio'
                    label='Paypal or Credit Card'
                    id='paypal'
                    name='paymentMethod'
                    value ='paypal'
                    checked={paymentMethod === 'paypal'}
                    onChange={(e)=>setPaymentMethod(e.target.value)}></Form.Check>
                    </Col>
                </Form.Group>
                <Button variant='primary' type='submit'>Continue</Button>
            </Form>
        </FormContainer>
      
    </div>
  )
}

export default PaymentScreen
