import React,{useState,useEffect} from 'react'
import { Link,useNavigate, useParams } from 'react-router-dom';
import {Button,Form } from 'react-bootstrap'
import Loader from '../components/Loader';
import Message from '../components/Message';
import { useSelector } from 'react-redux';
import FormContainer from '../components/FormContainer';
import Alert from 'react-bootstrap/Alert';



function ProductEditScreen() {
    const navigate = useNavigate();
    const { id: product_id } = useParams('id');  // Destructure id from useParams
    const [name, setName] = useState('');
    const [price, setPrice] = useState(0);
    const [brand, setBrand] = useState('');
    const [countInStock, setCountInStock] = useState(0);
    const [category, setCategory] = useState('');
    const [description, setDescription] = useState('');
    const [image,setImage] = useState('')
  
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState(false);
    const [success, setSuccess] = useState(false);
    const [message, setMessage] = useState('');
    const [uploading,setUploading] = useState(false)
    
    const userInfo = useSelector((state) => state.user.userInfo);
    const products = useSelector((state) => state.productList.products);
  
    useEffect(() => {
      if (userInfo && userInfo.isAdmin) {
        console.log('products',products)
        console.log('product id',product_id)
        const product = products.find((product) => product_id === String(product._id));
        console.log('product',product)
        if (product){
          setName(product.name);
          setBrand(product.brand);
          setPrice(product.price);
          setImage(product.image)
          setCountInStock(product.countInStock);
          setCategory(product.category);
          setDescription(product.description);
        } else {
          navigate('/admin/products');
        }
      } else {
        navigate('/');
      }
    }, [products, userInfo, product_id, navigate]);
  
    const updateHandler = async (event) => {
      event.preventDefault();  // Prevent form submission
      setLoading(true);
      try {
        const header = new Headers({
          'Content-Type': 'application/json',
          Authorization: `Bearer ${userInfo.token}`,
        });
        const body = JSON.stringify({
          name,
          brand,
          countInStock,
          image,
          price,
          category,
          description,
        });
        const response = await fetch(`http://127.0.0.1:8000/apis/admin/updateProduct/${product_id}`, {
          method: 'PUT',
          headers: header,
          body,
        });
        if (!response.ok) {
          const error = await response.json();
          throw new Error(error.message);
        }
        await response.json();
        setLoading(false);
        setSuccess(true);
        
      } catch (error) {
        console.log(error);
        setLoading(false);  // Stop loading on error
        setError(true);
        setMessage(error.message);
      }
    };


    const uploadHandler = async(e) => {
        const file = e.target.files[0]
        const formData = new FormData()
        formData.append('image',file)
        formData.append('product_id',product_id)
        setUploading(true)
        try{
            const headers = new Headers({
                // 'Content-Type' : 'multipart/form-data',
                Authorization : `Bearer ${userInfo?.token}`
            }   
            )
            
            const response = await fetch('http://127.0.0.1:8000/apis/admin/image/upload/',{
                method : 'POST',
                headers : headers,
                body : formData
            })
            if(!response.ok){
                throw new Error('Coult not upload Files')
            }
            const data = await response.json()
            setImage(data?.image)
            setUploading(false)
        }catch(error){
            console.log('error', error.message)
        }
    }
  
    useEffect(() => {
      if (success) {
        const timer = setTimeout(() => {
          setSuccess(false);
          navigate('/admin/products');
        }, 2000);
        return () => clearTimeout(timer);  // Cleanup the timer on unmount
      }
    }, [success]);
  
    return (
        <div>
          <Link to="/admin/products">Go Back</Link>
          <FormContainer>
            <h1>Edit Product</h1>
            {success && (
              <Alert key="success" variant="success">
                Successfully updated Product
              </Alert>
            )}
            {loading ? (
              <Loader />
            ) : error ? (
              <Message variant="danger">{message}</Message>
            ) : (
              <Form onSubmit={updateHandler}>
                <Form.Group controlId="name" className="py-3">
                  <Form.Label>Name</Form.Label>
                  <Form.Control
                    type="text"
                    placeholder="Enter Name"
                    value={name}
                    onChange={(event) => setName(event.target.value)}
                  />
                </Form.Group>
                <Form.Group controlId="brand" className="py-3">
                  <Form.Label>Brand</Form.Label>
                  <Form.Control
                    type="text"
                    placeholder="Enter Brand"
                    value={brand}
                    onChange={(event) => setBrand(event.target.value)}
                  />
                </Form.Group>
                <Form.Group controlId="image" className="py-3">
                  <Form.Label>Image</Form.Label>
                  <Form.Control
                    type="text"
                    placeholder="Enter Image"
                    value={image}
                    onChange={(event) => setImage(event.target.value)}
                  />
                  <Form.Control id="image" accept='image/jpeg,image/png' type="file"label="Choose File" custom onChange={uploadHandler}></Form.Control>
                  {uploading && <Loader />}
                </Form.Group>
                <Form.Group controlId="price" className="py-3">
                  <Form.Label>Price</Form.Label>
                  <Form.Control
                    type="number"
                    placeholder="Enter Price"
                    value={price}
                    onChange={(event) => setPrice(Number(event.target.value))}
                  />
                </Form.Group>
                <Form.Group controlId="countInStock" className="py-3">
                  <Form.Label>Count in Stock</Form.Label>
                  <Form.Control
                    type="number"
                    placeholder="Enter Count in Stock"
                    value={countInStock}
                    onChange={(event) => setCountInStock(Number(event.target.value))}
                  />
                </Form.Group>
                <Form.Group controlId="category" className="py-3">
                  <Form.Label>Category</Form.Label>
                  <Form.Control
                    type="text"
                    placeholder="Enter Category"
                    value={category}
                    onChange={(event) => setCategory(event.target.value)}
                  />
                </Form.Group>
                <Form.Group controlId="description" className="py-3">
                  <Form.Label>Description</Form.Label>
                  <Form.Control
                    type="text"
                    placeholder="Enter Description"
                    value={description}
                    onChange={(event) => setDescription(event.target.value)}
                  />
                </Form.Group>
                <Button type="submit" variant="primary">
                  Update
                </Button>
              </Form>
            )}
          </FormContainer>
        </div>
      );
    }
    

  
  export default ProductEditScreen;