import React from 'react';
import { Pagination } from 'react-bootstrap';
import { LinkContainer } from 'react-router-bootstrap';

function Paginate({ pages, page, isAdmin = false, searchquery = '' }) {

    console.log('page inside compoennt',page,typeof(page))
    console.log('pages inside components',pages,typeof(pages))
  return (
    <div className='w-100 d-flex justify-content-center'>
      {pages > 0 && (
        <Pagination>
          {[...Array(pages).keys()].map((x) => (
            <LinkContainer
              key={x + 1}
              to={{
                pathname:  '/',
                search: `?searchquery=${searchquery}&page=${x + 1}`,
              }}
            >
              <Pagination.Item key={x+1} active={(x + 1) === Number(page)}>{x + 1}</Pagination.Item>
            </LinkContainer>
          ))}
        </Pagination>
      )}
    </div>
  );
}

export default Paginate;
