import React,{useState,useEffect} from 'react'
import { Link,useNavigate, useLocation } from 'react-router-dom';
import { Row,Button,Col,Form } from 'react-bootstrap'
import Loader from '../components/Loader';
import Message from '../components/Message';
import { useDispatch,useSelector } from 'react-redux';
import FormContainer from '../components/FormContainer';
import { loginUser,loginUserBefore,loginUserError } from '../features/userSlice';
import axios from 'axios';

import { BASE_URL } from '../constants';
function LoginScreen() {
    const navigate = useNavigate()
    const location = useLocation()
    const [email,setEmail] = useState('')
    const [password,setPassword] = useState('')
    const redirect = location.search ? location.search.split('=')[1] : '/';
    
    const isError = useSelector((state)=> state.user.isError)
    const userInfo = useSelector((state) => state.user.userInfo)
    const isLoading = useSelector((state)=>state.user.isLoading)
    const dispatch = useDispatch()

    useEffect(()=>{
        console.log(userInfo)
        if(userInfo !== null){
            navigate('/')
        }
    },[navigate,userInfo,redirect])
    // useEffect(()=>{
    //     console.log(userInfo)
    //     if(userInfo){
    //         navigate('/')
    //     }
    // },[navigate,userInfo])

//     const submitHandler = async (event) => {
//     event.preventDefault();

//     try {
//         dispatch(loginUserBefore());

//         const config = {
//             headers: {
//                 'Content-Type': 'application/json'
//             }
//         };

//         const body = JSON.stringify({
//             username: email,
//             password: password
//         });

//         const { data } = await axios.post(`/user/login/`, body, config);

//         if (data) {
//             dispatch(loginUser(data));
//             localStorage.setItem('userInfo', JSON.stringify(data));
//         } else {
//             dispatch(loginUserError({ detail: 'Login failed. Please try again.' }));
//         }
//     } catch (error) {
//         dispatch(loginUserError({ detail: error.message || 'An error occurred during login.' }));
//     }
// };
const submitHandler = async (event) => {
    event.preventDefault();

    try {
        dispatch(loginUserBefore());

        const config = {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                username: email,
                password: password
            })
        };

        // const response = await fetch(`/user/login/`, config);
        const response = await fetch(`${BASE_URL}/user/login/`, config);
        const data = await response.json();

        if (response.ok) {
            dispatch(loginUser(data));
            localStorage.setItem('userInfo', JSON.stringify(data));
        } else {
            dispatch(loginUserError({ detail: 'Login failed. Please try again.' }));
        }
    } catch (error) {
        dispatch(loginUserError({ detail: error.message || 'An error occurred during login.' }));
    }
};


  return (
    <FormContainer>
        <h1>Sign In</h1>
        {isError && <Message variant='danger'>{isError}</Message>}
        {isLoading && <Loader></Loader>}
        <Form onSubmit={submitHandler}>
            <Form.Group controlId='email' className='py-3'>
                <Form.Label>Email Address</Form.Label>
                <Form.Control
                type='email'
                placeholder='Enter Email'
                value={email}
                onChange={(event)=>setEmail(event.target.value)}
                ></Form.Control>
            </Form.Group>
            <Form.Group controlId='password' className='py-3'>
                <Form.Label>Password</Form.Label>
                <Form.Control
                type='password'
                placeholder='Enter password'
                value={password}
                onChange={(event)=>setPassword(event.target.value)}
                ></Form.Control>
            </Form.Group>
            <Button type='submit' variant='primary'>Sign in</Button>

        </Form>
        <Row className='py-3'>
            <Col>New Customer ? <Link to={redirect ? `/register?redirect=${redirect}` : '/register'}>Register</Link></Col>
            {/* <Col>New Customer ? <Link to={'/register'}>Register</Link></Col> */}
        </Row>
    </FormContainer>
  )
}

export default LoginScreen
