import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { Row, Button, Col, Form, Table } from 'react-bootstrap';
import { LinkContainer } from 'react-router-bootstrap';
import Loader from '../components/Loader';
import Message from '../components/Message';
import { useDispatch, useSelector } from 'react-redux';
import { loginUser, loginUserBefore, loginUserError } from '../features/userSlice';
import { userOrdersLoading,
    userOrdersSuccess,
    userOrdersFailure, } from '../features/userDetailSlice';
import { BASE_URL } from '../constants';

function ProfileScreen() {
    const [name, setName] = useState('');
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [confirmPassword, setConfirmPassword] = useState('');
    const [message, setMessage] = useState('');
    const orderDetails = useSelector((state) => state.userDetail);
    const { orderLoading, userOrders, orderError } = orderDetails;

    const dispatch = useDispatch();
    const userLogin = useSelector((state) => state.user);
    const { userInfo, isLoading, isError } = userLogin;
    const navigate = useNavigate();

    

    

    const updateHandler = async (e) => {
        e.preventDefault();
        dispatch(loginUserBefore({}));
        try {
            const headers = {
                'Content-Type': 'application/json',
                Authorization: `Bearer ${userInfo.token}`
            };
            const body = {
                name,
                email,
                password
            };
            // const response = await fetch(`user/update/`, {
            const response = await fetch(`${BASE_URL}user/update/`, {
                method: 'PUT',
                headers,
                body: JSON.stringify(body)
            });

            if (!response.ok) {
                throw new Error('User could not be updated');
            }
            const data = await response.json();

            dispatch(loginUser({ ...userInfo, name: data.name, email: data.email, username: data.username }));
            localStorage.setItem('userInfo', JSON.stringify({ ...userInfo, name: data.name, email: data.email, username: data.username }));
        } catch (error) {
            dispatch(loginUserError({ detail: error.message || 'An error occurred while updating' }));
        }
    };

    const getUsersOrder = async () => {
        dispatch(userOrdersLoading());
        try {
            const headers = {
                'Content-Type': 'application/json',
                Authorization: `Bearer ${userInfo.token}`
            };
            // const response = await fetch(`/order/my_orders`, {
                const response = await fetch(`${BASE_URL}/order/my_orders`, {
                method: 'GET',
                headers
            });

            if (!response.ok) {
                const error = await response.json();
                
                throw new Error(error.message);
            }
            const data = await response.json();
            console.log(data)
            dispatch(userOrdersSuccess(data));
        } catch (error) {
            dispatch(userOrdersFailure(error.message));
        }
    };
    console.log('userorders',userOrders)
    useEffect(() => {
        if (!userInfo) {
            navigate('/login');
        } else {
            getUsersOrder();
            setName(userInfo?.name);
            setEmail(userInfo?.email);
        }
    }, [userInfo]);

    return (
        <Row>
            <Col md={3}>
                <h2>User Profile</h2>
                {message && <Message variant='danger'>{message}</Message>}
                {isError && <Message variant='danger'>{isError}</Message>}
                {isLoading && <Loader />}
                <Form onSubmit={updateHandler}>
                    <Form.Group controlId='name' className='py-3'>
                        <Form.Label>Name</Form.Label>
                        <Form.Control
                            type='text'
                            placeholder='Enter Name'
                            value={name}
                            onChange={(e) => setName(e.target.value)}
                        ></Form.Control>
                    </Form.Group>
                    <Form.Group controlId='email' className='py-3'>
                        <Form.Label>Email Address</Form.Label>
                        <Form.Control
                            type='email'
                            placeholder='Enter Email'
                            value={email}
                            onChange={(e) => setEmail(e.target.value)}
                        ></Form.Control>
                    </Form.Group>
                    <Form.Group controlId='password' className='py-3'>
                        <Form.Label>Password</Form.Label>
                        <Form.Control
                            type='password'
                            placeholder='Enter password'
                            value={password}
                            onChange={(e) => setPassword(e.target.value)}
                        ></Form.Control>
                    </Form.Group>
                    <Form.Group controlId='confirmPassword' className='py-3'>
                        <Form.Label> Confirm Password</Form.Label>
                        <Form.Control
                            type='password'
                            placeholder='Enter password'
                            value={confirmPassword}
                            onChange={(e) => setConfirmPassword(e.target.value)}
                        ></Form.Control>
                    </Form.Group>
                    <Button type='submit' variant='primary'>Update</Button>
                </Form>
            </Col>
            <Col md={9}>
                <h2>My Orders</h2>
                {orderLoading ? (
                    <Loader />
                ) : orderError ? (
                    <Message variant='danger'>{orderError}</Message>
                ) : userOrders && userOrders.length > 0 ? (
                    <Table striped responsive className='table-sm'>
                        <thead>
                            <tr>
                                <th>ID</th>
                                <th>Date</th>
                                <th>Total</th>
                                <th>Paid</th>
                                <th>Delivered</th>
                                <th></th>
                            </tr>
                        </thead>
                        <tbody>
                            {userOrders.map((order) => (
                                <tr key={order?._id}>
                                    <td>{order?._id}</td>
                                    <td>{order?.createdAt ?new Date(order.createdAt).toLocaleDateString() : 'N/A'}</td>
                                    <td>{order?.totalPrice}</td>
                                    <td>
                                        {order?.isPaid === true ? 
                                            (order.PaidAt ? new Date(order.PaidAt).toLocaleDateString() : 'N/A') 
                                            : (<i className='fas fa-times' style={{ color: 'red' }}></i>)
                                        }
                                    </td>

                                    <td>{order?.isDelivered ? 'Delivered' : 'Not Delivered'}</td>
                                    <td>
                                        <LinkContainer to={`/order/${order._id}`}>
                                            <Button className='btn-sm'>Details</Button>
                                        </LinkContainer>
                                    </td>
                                </tr>
                            ))}
                        </tbody>
                    </Table>
                ) : (
                    <Message variant='info'>No orders found</Message>
                )}
            </Col>
        </Row>
    );
}

export default ProfileScreen;
