import React,{useState,useEffect} from 'react'
import { useNavigate} from 'react-router-dom';
// import { addShippingAddress } from '../features/cartSlice';
import { useDispatch, useSelector } from 'react-redux';
import { Button,Row,Col,ListGroup,Image,Card } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import Message from '../components/Message';
// import axios from 'axios';
// import FormContainer from '../components/FormContainer';
import CheckoutSteps from '../components/CheckoutSteps';
import { orderSuccess,orderFailure,orderReset } from '../features/orderSlice';
import { cartReset } from '../features/cartSlice';
import { BASE_URL } from '../constants';

function PlaceOrderScreen() {
    const cart = useSelector((state)=> state.cart)
    const user = useSelector((state) => state.user)
    const {userInfo} = user
    const order = useSelector((state)=> state.order)
    const {success,error,errMsg,id} = order
    const dispatch = useDispatch()
    const navigate = useNavigate()


    const itemPrice = Number(cart.cartItems.reduce((acc, item) => acc + (item.price * item.qty), 0)).toFixed(2);
    const shippingPrice = Number(itemPrice > 100 ? 0 : 10).toFixed(2);
    const taxPrice = Number(itemPrice * 0.082).toFixed(2);
    const totalPrice = Number(Number(itemPrice) + Number(shippingPrice) + Number(taxPrice)).toFixed(2)

    const updatedCart = {
        ...cart,
        itemPrice,
        shippingPrice,
        taxPrice,
        totalPrice
    };

    // useEffect(() => {
    //     if (cart.paymentMethod === '' || !cart.paymentMethod || cart.paymentMethod === null) {
    //         navigate('/payment');
    //     }
    // }, [cart.paymentMethod, navigate]);


   

    useEffect(()=>{
        if(success === true){
            // dispatch(orderReset())
            console.log(order.id)
            navigate(`/order/${id}`)
        }
    },[success,navigate,id])

  

    const handlePlaceOrder = async(event) => {
        event.preventDefault()
        // console.log(userInfo.token)
        // console.log(updatedCart)
        try{
            const headers = {
                'Content-Type': 'application/json',
                Authorization: `Bearer ${userInfo.token}`}

            const body = JSON.stringify(updatedCart)
            // const response = await fetch(`order/add/`,{
            const response = await fetch(`${BASE_URL}order/add/`,{
                method:'POST',
                headers : headers,
                body : body
            })

            if(!response.ok){
                throw new Error('User could not be updated');
            }
            const data = await response.json()
            console.log('data',data.data)
           console.log('beforeDispatching',{
            id : data.data._id,
            orderItems : data.data.order_items,
            shippingAddress : data.data.shippingAddress,
            paymentMethod : data.data.paymentMethod,
            itemsPrice : data.data.itemsPrice,
            shippingPrice : data.data.shippingPrice,
            taxPrice : data.data.taxPrice,
            totalPrice : data.data.totalPrice,
            success : true,
            error : false
            })
        dispatch(orderSuccess({
            id : data.data._id,
            orderItems : data.data.order_items,
            shippingAddress : data.data.shippingAddress,
            paymentMethod : data.data.paymentMethod,
            itemsPrice : data.data.itemsPrice,
            shippingPrice : data.data.shippingPrice,
            taxPrice : data.data.taxPrice,
            totalPrice : data.data.totalPrice,
            success : true,
            error : false

        }))
            

            dispatch(cartReset())
            localStorage.removeItem('cartItems')
            
 
        }catch(error){
            dispatch(orderFailure({
                success : false,
                error : true,
                errMsg : error.detail
            }));
        }
        
    }

  return (
    <div>
        <CheckoutSteps step1 step2 step3 step4/>
        <Row>
            <Col md={8}>
                <ListGroup variant='flush'>
                    <ListGroup.Item>
                        <h2>Shipping: </h2>
                        <p><strong>Shipping: </strong>
                        {cart.shippingAddress.address}, {cart.shippingAddress.city}
                        , { ' '} {cart.shippingAddress.postalCode},{cart.shippingAddress.country}
                        </p>
                    </ListGroup.Item>
                    <ListGroup.Item>
                        <h2>Payment Method</h2>
                        <p><strong>Method: </strong>
                        {cart.paymentMethod}
                        </p>
                    </ListGroup.Item>
                    <ListGroup.Item>
                        <h2>order Items</h2>
                        {cart.cartItems.length === 0 ? (
                            <Message variant='info'>Cart is Empty</Message>
                        ) : (
                            <ListGroup variant='flush'>
                                {cart.cartItems.map((item,index)=> (
                                    <ListGroup.Item key ={index}>
                                        <Row>
                                            <Col><Image src={item.image} sm fluid rounded></Image></Col>
                                            <Col>
                                                <Link to={`/product/${item.product}`}>{item.name}</Link>
                                            </Col>
                                            <Col md={4}>
                                                {item.qty} X ${item.price} = ${(item.qty * item.price).toFixed(2)}
                                            </Col>
                                        </Row>
                                    </ListGroup.Item>
                                ))}
                            </ListGroup>
                        )}
                    </ListGroup.Item>
                </ListGroup>
            </Col>
            <Col md={4}>
                <Card>
                    <ListGroup variant='flush'>
                        <ListGroup.Item>
                            <h2>Order Summary</h2>

                        </ListGroup.Item>
                        <ListGroup.Item>
                            <Row>
                                <Col>Item : </Col>
                                <Col>${updatedCart.itemPrice}</Col>
                            </Row>
                        </ListGroup.Item>
                        <ListGroup.Item>
                            <Row>
                                <Col>ShippingPrice : </Col>
                                <Col>${updatedCart.shippingPrice}</Col>
                            </Row>
                        </ListGroup.Item>
                        <ListGroup.Item>
                            <Row>
                                <Col>Tax : </Col>
                                <Col>${updatedCart.taxPrice}</Col>
                            </Row>
                        </ListGroup.Item>
                        <ListGroup.Item>
                            <Row>
                                <Col>Total : </Col>
                                <Col>${updatedCart.totalPrice}</Col>
                            </Row>
                        </ListGroup.Item>
                        <ListGroup.Item>
                            {error && (
                                <Message variant='danger'>{error}</Message>
                            )}
                        </ListGroup.Item>
                        <ListGroup.Item>
                            <Button variant='primary' onClick={handlePlaceOrder}className='w-100'>Place Order</Button>
                        </ListGroup.Item>
                        
                    </ListGroup>
                </Card>
            </Col>
        </Row>
      
    </div>
  )
}

export default PlaceOrderScreen
