import React,{useState,useEffect} from 'react'
import { Link,useNavigate, useLocation } from 'react-router-dom';
import { Row,Button,Col,Form } from 'react-bootstrap'
import Loader from '../components/Loader';
import Message from '../components/Message';
import { useDispatch,useSelector } from 'react-redux';
import FormContainer from '../components/FormContainer';
import { registerUser,registerUserError } from '../features/userSlice';
// import { registerUser, registerUserBefore,registerUserError} from '../features/registerSlice';
import axios from 'axios';
import { BASE_URL } from '../constants';

function RegisterScreen() {
    const navigate = useNavigate()
    const location = useLocation()
    const [name,setName] = useState('')
    const [email,setEmail] = useState('')
    const [password,setPassword] = useState('')
    const [confirmPassword,setConfirmPassword] =  useState('')
    const [message,setMessage] = useState('')
    const redirect = location.search ? location.search.split('=')[1] : '/';
    const isError = useSelector((state)=> state.user.isError)
    const userInfo = useSelector((state) => state.user.userInfo)
    const isLoading = useSelector((state)=>state.user.isLoading)
    const dispatch = useDispatch()

    useEffect(()=>{
        console.log(userInfo)
        if(userInfo !== null){
            navigate(redirect)
        }
    },[navigate,userInfo,redirect])
    

    const registerHandler = async(event) => {

        event.preventDefault();
            try{
                if(password === confirmPassword){
                    
                    const body = JSON.stringify({
                        "name" : name,
                        "email" : email,
                        "password" : password
                    })
                    const config = {
                        headers: {
                            'Content-Type': 'application/json'
                        }
                    };
                    // const data = await axios.post(`user/register/`,body,config)
                    const data = await axios.post(`${BASE_URL}user/register/`,body,config)
                    console.log('user',data)
                    if(data){
                        dispatch(registerUser(data))
                        localStorage.setItem('userInfo',JSON.stringify(data))
                    }
                }
                else{
                    setMessage('Password does not Match')
                }
                
            }catch(error){
                dispatch(registerUserError({ detail: error.message || 'An error occurred during login.' }));
            }
        }

    
    

  return (
    <FormContainer>
        <h1>Register</h1>
        {message && <Message variant='danger'>{message}</Message>}
        {isError && <Message variant='danger'>{isError}</Message>}
        {isLoading && <Loader></Loader>}
        <Form onSubmit={registerHandler}>
            <Form.Group  controlId='name' className='py-3'>
                <Form.Label>Name</Form.Label>
                <Form.Control
                required
                type='text'
                placeholder='Enter Name'
                value={name}
                onChange={(event)=>setName(event.target.value)}
                ></Form.Control>
            </Form.Group>
            <Form.Group controlId='email' className='py-3'>
                <Form.Label>Email Address</Form.Label>
                <Form.Control
                required
                type='email'
                placeholder='Enter Email'
                value={email}
                onChange={(event)=>setEmail(event.target.value)}
                ></Form.Control>
            </Form.Group>
            <Form.Group controlId='password' className='py-3'>
                <Form.Label>Password</Form.Label>
                <Form.Control
                type='password'
                placeholder='Enter password'
                value={password}
                onChange={(event)=>setPassword(event.target.value)}
                ></Form.Control>
            </Form.Group>
            <Form.Group controlId='confirmPassword' className='py-3'>
                <Form.Label> Confirm Password</Form.Label>
                <Form.Control
                type='password'
                placeholder='Enter password'
                value={confirmPassword}
                onChange={(event)=>setConfirmPassword(event.target.value)}
                ></Form.Control>
            </Form.Group>
            <Button type='submit' variant='primary'>Register</Button>

        </Form>
        <Row className='py-3'>
            <Col>Already have an account? <Link to={redirect ? `/login?redirect=${redirect}` : '/login'}>Sign In</Link></Col>
            {/* <Col>New Customer ? <Link to={'/register'}>Register</Link></Col> */}
        </Row>
    </FormContainer>
  )
}

export default RegisterScreen
