import React,{useEffect} from 'react';
import { topProductsError,topProductsLoading,topProductsSuccess } from '../features/topProducts';
import { Link } from 'react-router-dom';
import { Carousel,Image } from 'react-bootstrap';
import { useSelector,useDispatch } from 'react-redux';
import Message from './Message';
import Loader from './Loader';

function Carousal() {
    const dispatch = useDispatch()
    const topProducts = useSelector((state) => state.topProducts)
    const {loading,products,error,errMsg} = topProducts


    const getTopProduct = async() => {
        try{
            dispatch(topProductsLoading())
            const headers = new Headers()
            headers.append('Content-Type','application/json')
            const response = await fetch(`http://127.0.0.1:8000/apis/getTopProducts/`,{
                method : 'GET',
                headers : headers
            })
            if(!response.ok){
                const error = await response.json()
                throw new Error(error?.error)
            }
            const responseJson = await response.json()
            console.log('response.json',responseJson)
            const products = responseJson?.data
            dispatch(topProductsSuccess({
                'products' : products
            }))

        }catch(error){
            dispatch(topProductsError({
                'error' : error
            }))    
        }
    }


    useEffect(() => {
        getTopProduct()
    },[dispatch])


  return (
    <div>
        {loading ? <Loader/> : error ? <Message variant='danger'>{errMsg}</Message> : (
            <Carousel pause="hover" className="bg-dark">
                {products?.map((product) => (
                    <Carousel.Item key={product._id}>
                        <Link to={`/product/${product._id}`}>
                            <Image src={product.image} alt={product.name} fluid/>
                            <Carousel.Caption className='carousal.caption'>
                                <h4>{product.name}  ${product.price}</h4>
                            </Carousel.Caption>
                        </Link>
                    </Carousel.Item>
                ))}

            </Carousel>
        )}
        
      
    </div>
  )
}

export default Carousal
