import { createSlice } from "@reduxjs/toolkit";


const initialState = {
    loading : false,
    products : [],
    error : false,
    errMsg : ''
}


const topProductSlice = createSlice({
    name:'topProducts',
    initialState:initialState,
    reducers : {
        topProductsLoading : (state,action) => {
            state.loading = true
            state.products = []
            state.error = false
            state.errMsg = '' 
        },
        topProductsSuccess : (state,action) => {
            state.loading = false
            state.products = action.payload.products
            state.error = false
            state.errMsg = '' 
        },
        topProductsError : (state,action) => {
            state.loading = false
            state.products = []
            state.error = true
            state.errMsg = action.payload.error 
        },


    }
})


export const {topProductsLoading,topProductsSuccess,topProductsError} = topProductSlice.actions

export default topProductSlice.reducer