import { createSlice } from "@reduxjs/toolkit";

const initialState = {
    loading: false,
    users: [],
    error: false,
    errMsg: ""
};

const userAllListSlice = createSlice({
    name: "userAllList",
    initialState,
    reducers: {
        userListLoading: (state) => {
            state.loading = true;
            state.error = false;
        },
        userListSuccess: (state, action) => {
            state.loading = false;
            state.users = action.payload;
            state.error = false;
        },
        userListError: (state, action) => {
            state.loading = false;
            state.error = true;
            state.errMsg = action.payload;
        },
        userListReset: (state) => {
            return initialState;
        },
    }
});

export const { userListLoading, userListError, userListSuccess, userListReset } = userAllListSlice.actions;
export default userAllListSlice.reducer;
