import React,{useState,useEffect} from 'react'
import { Link,useNavigate, useParams } from 'react-router-dom';
import {Button,Form } from 'react-bootstrap'
import Loader from '../components/Loader';
import Message from '../components/Message';
import { useSelector } from 'react-redux';
import FormContainer from '../components/FormContainer';
import Alert from 'react-bootstrap/Alert';

// import { registerUser,registerUserError } from '../features/userSlice';
// import { registerUser, registerUserBefore,registerUserError} from '../features/registerSlice';

function UserEditScreen() {
    const navigate = useNavigate()
    // const location = useLocation()
    const user_id = useParams('id')
    const [name,setName] = useState('')
    const [email,setEmail] = useState('')
    const [isAdmin,setIsAdmin] = useState(false)
    const [loading,setLoading] = useState(false)
    const [error,setError] = useState(false)
    const [success,setSuccess] = useState(false)
    const [message,setMessage] = useState('')
    const userInfo = useSelector((state) => state.user.userInfo)

    useEffect(()=>{
        console.log('user_id',user_id)
        console.log(userInfo)
        if(!userInfo){
            navigate('/')
        }
        else if(user_id === null){
            navigate('/admin/users')
        }
        else{
            getUserDetails(user_id)
        }
    },[navigate,userInfo,user_id])


    const getUserDetails = async(user_id) => {
        setLoading(true)
        try{
            const header = new Headers({
                'Content-Type' : 'application/json',
                Authorization : `Bearer ${userInfo.token}`
            })
          
            const response = await fetch(`http://127.0.0.1:8000/apis/admin/user/${user_id?.id}`,{
                method : 'GET',
                headers : header
            })
            if(!response.ok){
                const error = await response.json()
                throw new Error(error.message)
            }
            const data = await response.json()
            console.log('data',data)
            setName(data.name)
            setEmail(data.email)
            setIsAdmin(data.isAdmin)
            setLoading(false)
        }catch(error){
            console.log(error)
            setError(true)
            setMessage(error.message)
        }
    }

    const updateHandler = async() => {
        setLoading(true)
        try{
            const header = new Headers({
                'Content-Type' : 'application/json',
                Authorization : `Bearer ${userInfo.token}`
            })
            const body = JSON.stringify({
                name : name,
                email : email,
                isAdmin : isAdmin
            })
            const response = await fetch(`http://127.0.0.1:8000/apis/admin/userUpdate/${user_id?.id}`,{
                method : 'PUT',
                headers : header,
                body
            })
            if(!response.ok){
                const error = await response.json()
                throw new Error(error.message)
            }

            const data = await response.json()
            console.log('data',data)
            setName(data.name)
            setEmail(data.email)
            setIsAdmin(data.isAdmin)
            setLoading(false)
            setSuccess(true)
        }catch(error){
            console.log(error)
            setError(true)
            setMessage(error.message)
        }
    }

    useEffect(()=>{
        if(success === true){
            setTimeout(()=>{
                setSuccess(false)
            },3000)
        }
    },[success])
    


    
    

  return (
    <div>
        <Link to={`/admin/users`}>Go Back </Link>
        {success ? (<Alert key='success' variant='success'>
            SuccessFully updated User
            </Alert>) : null}
            


            <FormContainer>
                <h1>Edit User</h1>
                {loading ? <Loader/> : error ? <Message variant='danger'>{message}</Message> : (
                    <Form onSubmit={updateHandler}>
                    <Form.Group  controlId='name' className='py-3'>
                        <Form.Label>Name</Form.Label>
                        <Form.Control
                        
                        type='text'
                        placeholder='Enter Name'
                        value={name}
                        onChange={(event)=>setName(event.target.value)}
                        ></Form.Control>
                    </Form.Group>
                    <Form.Group controlId='email' className='py-3'>
                        <Form.Label>Email Address</Form.Label>
                        <Form.Control
                        
                        type='email'
                        placeholder='Enter Email'
                        value={email}
                        onChange={(event)=>setEmail(event.target.value)}
                        ></Form.Control>
                    </Form.Group>
                    <Form.Group controlId='isadmin' className='py-3'>
                        <Form.Check
                        type='checkbox'
                        label='Is Admin'
                        checked={isAdmin}
                        onChange={(event)=>setIsAdmin(event.target.checked)}
                        ></Form.Check>
                    </Form.Group>
                    <Button type='submit' variant='primary'>Update</Button>

                </Form>
                )}
            </FormContainer>

    </div>
    
  )
}

export default UserEditScreen

