import { createSlice} from "@reduxjs/toolkit";

const initialState = {
    cartItems : [],
    shippingAddress : {},
    paymentMethod : ''
}

const cartSlice = createSlice({
    name: 'cart',
    initialState : initialState,
    reducers : {
        cartAddItem : (state,action) => {
            const item = action.payload
            const existsItem = state.cartItems.find(x => x.product === item.product);
            if (existsItem) {
                existsItem.qty = item.qty;
            } else {
                state.cartItems = [...state.cartItems,item]
            }

        },
        cartRemoveItem : (state,action) => {
            const productId = action.payload.product
            state.cartItems = state.cartItems.filter((item) => item.product !== productId)
        },
        addShippingAddress : (state,action) => {
            state.shippingAddress = action.payload
        },
        addPaymentMethod : (state,action) => {
            const {paymentMethod} = action.payload
            state.paymentMethod = paymentMethod
        },
        cartReset : (state,action) => {
            return initialState
        }
    }
})

export const {cartAddItem,cartRemoveItem,addShippingAddress,addPaymentMethod,cartReset} = cartSlice.actions
export default cartSlice.reducer