import React,{useState,useEffect} from 'react'
import { useNavigate} from 'react-router-dom';
// import { addShippingAddress } from '../features/cartSlice';
import { useDispatch, useSelector } from 'react-redux';
import { Button,Row,Col,ListGroup,Image,Card } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import Loader from '../components/Loader';
import Message from '../components/Message';
import { useParams } from 'react-router-dom';
import CLIENT_ID from '../PaypalCredentials';
import { PayPalButtons } from '@paypal/react-paypal-js';
import { payOrderLoading,payOrderSuccess,payOrderError,payOrderReset } from '../features/orderDetailSlice';

// import axios from 'axios';

import { orderDetailSuccess,orderDetailError,orderDetailLoading } from '../features/orderDetailSlice';
import Header from '../components/Header';


function OrderScreen() {

    
    const { id } = useParams();
    const navigate = useNavigate();
    const dispatch = useDispatch();

    const orderDetail = useSelector((state) => state.orderDetail);
    const { order, error, loading, errMsg } = orderDetail;
    const { payLoading,paySuccess, payError , payErrMsg} = orderDetail;
    const [deliverloading,setLoading] = useState(false)
    const [delivererror,setError] = useState("")

    const user = useSelector((state) => state.user);
    const { userInfo } = user;

    const getData = async (orderId) => {
        console.log(typeof('type of id',id))
        try {
            if (!userInfo || !userInfo.token) {
                throw new Error('User not authenticated');
            }

            const headers = {
                'Content-Type': 'application/json',
                Authorization: `Bearer ${userInfo.token}`
            };

            console.log('Fetching order with ID:', orderId);
            console.log('Using token:', userInfo.token);

            // dispatch(orderDetailLoading());

            const response = await fetch(`http://127.0.0.1:8000/apis/order/${orderId}`, {
                method: 'GET',
                headers: headers,
            });

            console.log('Response status:', response.status);

            if (!response.ok) {
                if (response.status === 401) {
                    throw new Error('Unauthorized User')
                    console.log('Unauthorized - logging out');
                }
                throw new Error('Order could not be fetched');
            }

            const data = await response.json();
            console.log(data)
            data.itemPrice = Number(data.order_items.reduce((acc, item) => acc + (item.price * item.qty), 0)).toFixed(2);

            dispatch(orderDetailSuccess(data));
        } catch (error) {
            console.error('Error fetching order:', error);
            dispatch(orderDetailError({
                errMsg: error.message || 'An unknown error occurred'
            }));
        }
    };

    useEffect(() => {
        if (!order || String(order._id) !== id || paySuccess) {
            getData(id);
            dispatch(payOrderReset());
        }
    }, [dispatch, id, order, paySuccess]);


    const createOrder = async (data, actions) => {
        return actions.order.create({
            purchase_units: [{
                description : `Test-${order.orderId}`,
                amount: {
                    value: order.totalPrice ,
                    currency_code: "USD"

                }
            }]
        });
    };

    const deliverOrderHandler =async() => {
        try{

            setLoading(true)
            const header = new Headers()
            header.append('Content-Type','application/json')
            header.append('Authorization', `Bearer ${userInfo?.token}`);
            console.log('order id',order._id)
            const url = `http://127.0.0.1:8000/apis/order/deliver/${order?._id}`
            const response = await fetch(url,{
                method : 'PUT',
                headers :  header,
                body : {}
            })
            if(!response.ok){
                const err = await response.json()
                throw new Error(err?.error)
            }
            const responseJson = await response.json()
            if(responseJson?.msg === "Success"){
                setLoading(false)
                getData(order._id)
            }

        }catch(error){
            console.log('error',error)
            setError(error?.message)
        }
    }

    const onApprove = async (data, actions) => {
        return actions.order.capture().then(async (details) => {
            try {
                const headers = {
                    'Content-Type': 'application/json',
                    Authorization: `Bearer ${userInfo.token}`
                };
                const id = String(order._id)

                const response = await fetch(`http://127.0.0.1:8000/apis/order/${id}/pay`, {
                    method: 'PUT',
                    headers: headers,
                    // body: JSON.stringify(details)
                });

                if (!response.ok) {
                    throw new Error('Payment confirmation failed');
                }

                const data = await response.json();
                dispatch(payOrderSuccess(data));
            } catch (error) {
                dispatch(payOrderError({
                    errMsg: error.message || 'Payment Failed'
                }));
            }
        });
    };



  return (
    <div>
    {
        loading ? (<Loader></Loader>) : error ? (<Message  variant='danger'>{errMsg}</Message>)
        :
        (
            <div>
            <h1>Order : {order._id}</h1>
            <Row>
            <Col md={8}>
                <ListGroup variant='flush'>
                    <ListGroup.Item>
                        <h2>Shipping: </h2>
                        <p><strong>Name : </strong>{order.user.name}</p>
                        <p><strong>Email : </strong><a href={`mailto:${order.user.email}`}>{order.user.email}</a></p>
                        <p><strong>Shipping: </strong>
                        {order.shippingAddress.address}, {order.shippingAddress.city}
                        , { ' '} {order.shippingAddress.postal_code},{order.shippingAddress.country}
                        </p>
                        {order.isDelivered===true ? (<Message variant='success'>Delivered on {order.deliveredAt}</Message>) : 
                        (<Message variant='warning'>Not Delivered</Message>)}
                    </ListGroup.Item>
                    <ListGroup.Item>
                        <h2>Payment Method</h2>
                        <p><strong>Method: </strong>
                        {order.paymentMethod}
                        </p>
                        {order.isPaid ? (<Message variant='success'>Paid on {order.PaidAt}</Message>) : 
                        (<Message variant='warning'>Payment Due</Message>)}
                    </ListGroup.Item>
                    <ListGroup.Item>
                        <h2>order Items</h2>
                        {order.order_items.length === 0 ? (
                            <Message variant='info'> Order is Empty</Message>
                        ) : (
                            <ListGroup variant='flush'>
                                {order.order_items.map((item,index)=> (
                                    <ListGroup.Item key ={index}>
                                        <Row>
                                            <Col><Image src={item.image} sm fluid rounded></Image></Col>
                                            <Col>
                                                <Link to={`/product/${item.product}`}>{item.name}</Link>
                                            </Col>
                                            <Col md={4}>
                                                {item.qty} X ${item.price} = ${(item.qty * item.price).toFixed(2)}
                                            </Col>
                                        </Row>
                                    </ListGroup.Item>
                                ))}
                            </ListGroup>
                        )}
                    </ListGroup.Item>
                </ListGroup>
            </Col>
            <Col md={4}>
                <Card>
                    {delivererror && <Message variant="danger">${delivererror}</Message>}
                    {deliverloading && <Loader></Loader>}
                    <ListGroup variant='flush'>
                        <ListGroup.Item>
                            <h2>Order Summary</h2>

                        </ListGroup.Item>
                        <ListGroup.Item>
                            <Row>
                                <Col>Item : </Col>
                                <Col>${order.itemPrice}</Col>
                            </Row>
                        </ListGroup.Item>
                        <ListGroup.Item>
                            <Row>
                                <Col>ShippingPrice : </Col>
                                <Col>${order.shippingPrice}</Col>
                            </Row>
                        </ListGroup.Item>
                        <ListGroup.Item>
                            <Row>
                                <Col>Tax : </Col>
                                <Col>${order.taxPrice}</Col>
                            </Row>
                        </ListGroup.Item>
                        <ListGroup.Item>
                            <Row>
                                <Col>Total : </Col>
                                <Col>${order.totalPrice}</Col>
                            </Row>
                        </ListGroup.Item>

                        {!order.isPaid && (
                            <ListGroup.Item>
                                {payLoading && <Loader/>}
                                
                                    
                                    <PayPalButtons
                                    createOrder={createOrder}
                                    onApprove={onApprove}

                                    />
                                </ListGroup.Item>
                        )}
                        
                    </ListGroup>
                    {(userInfo && userInfo?.isAdmin && order?.isPaid && !order?.isDelivered) ? (
                        <ListGroup.Item>
                            <Button type="button" className='btn btn-block w-100'
                            onClick={() => deliverOrderHandler()}
                            >
                                Mark as Delivered

                            </Button>
                        </ListGroup.Item>
                    ) : null}
                </Card>
            </Col>
        </Row>
        </div>)
        }
        
       
      
    </div>
  )
}

export default OrderScreen
