import React,{useEffect} from 'react'
// import { Link,useNavigate, useLocation } from 'react-router-dom';
import { Button, Table } from 'react-bootstrap'
import Loader from '../components/Loader';
import Message from '../components/Message';
import { useDispatch, useSelector } from 'react-redux';
import { LinkContainer } from 'react-router-bootstrap';
import { userListLoading,userListSuccess,userListError } from '../features/userAllListSlice';
import {  useNavigate } from 'react-router-dom';

function UserListScreen() {

    const dispatch = useDispatch()
    const navigate = useNavigate()
    const userInfo = useSelector((state)=>state.user.userInfo)
    const userList = useSelector((state)=>state.userAllList)
    console.log(userList)
    const {loading,users = [],error,errMsg} = userList

    const getUserList = async() => {
        dispatch(userListLoading())
        try{
            const headers = new Headers({
                'Content-Type' : 'application/json',
                Authorization : `Bearer ${userInfo.token}`
            })
            const response = await fetch('http://127.0.0.1:8000/apis/admin/users',{
                method : 'GET',
                headers
            })
            if(!response.ok){
                const error = await response.json()
                return new Error(error)
            }
            const data = await response.json()
            dispatch(userListSuccess(data))
        }
        catch(error){
            console.log(error)
            dispatch(userListError(error))
        }
    }

    const deleteHandler = async(id) => {
        
        try{
            const headers = new Headers({
                'Content-Type' : 'application/json',
                Authorization : `Bearer ${userInfo.token}`
            })
            const response = await fetch(`http://127.0.0.1:8000/apis/admin/delete/${id}`,{
                method : 'DELETE',
                headers
            })
            if(!response.ok){
                const error = await response.json()
                return new Error(error.message)
            }
            const data = await response.json()
            console.log('response',data)
            getUserList()
        }
        catch(error){
            console.log(error)
            dispatch(userListError(error.message))
        }
    }

    

    useEffect(()=>{
        if(!userInfo || !userInfo.name || !userInfo.name === ""){
            navigate('/')
            return
        }
        getUserList()
    },[userInfo])
  return (
    <div>
        <h1>Users</h1>
        {loading ? (<Loader/>) : 
                error ? (<Message variant='danger'>{errMsg!=="" ? errMsg : "Error Occurred"}</Message>)
                    : (
                        <Table striped responsive bordered hover className='table-sm'>
                            <thead>
                                <th>ID</th>
                                <th>NAME</th>
                                <th>EMAIL</th>
                                <th>ADMIN</th>
                                <th></th>
                            </thead>
                            <tbody>
                                {users.map((user)=> (
                                    <tr key={user._id}>
                                        <td >{user._id}</td>
                                        <td >{user.name}</td>
                                        <td >{user.email}</td>
                                        <td >{user.isAdmin ? (
                                            <i className='fas fa-check' style={{color : 'green'}}></i>
                                        ): (
                                            <i className='fas fa-check' style={{color : 'red'}}></i>
                                        )}</td>
                                        <td >
                                            <LinkContainer to={`/admin/users/${user._id}`}>
                                                <Button variant='light'>
                                                    <i className='fas fa-edit'></i>
                                                </Button>
                                            </LinkContainer>
                                            <Button variant='danger' onClick={() => deleteHandler(user._id)}>
                                            <i className='fas fa-trash'></i>
                                            </Button>
                                        </td>
                                        
                                    </tr>
                                ))}
                                
                            </tbody>

                        </Table>
                    )}
      
    </div>
  )
}

export default UserListScreen
