import {configureStore} from  '@reduxjs/toolkit';
import productReducer from './features/productSlice';
import productDetailReducer from './features/productDetailSlice';
import cartReducer from './features/cartSlice'
import userReducer from './features/userSlice';
import registerReducer from './features/registerSlice';
import userDetailReducer from './features/userDetailSlice';
import orderSliceReducer from './features/orderSlice';
import orderDetailReducer from './features/orderDetailSlice';
import userAllListReducer from './features/userAllListSlice';
import productListReducer from './features/productListSlice';
import orderListReducer from './features/orderListSlice';
import topProductReducer from './features/topProducts';

export const store = configureStore({
    reducer : {
        product : productReducer,
        productDetails : productDetailReducer,
        cart : cartReducer,
        user : userReducer,
        register: registerReducer,
        userDetail : userDetailReducer,
        order : orderSliceReducer,
        orderDetail : orderDetailReducer,
        userAllList : userAllListReducer,  
        productList :   productListReducer,
        orderList : orderListReducer,
        topProducts : topProductReducer
    }
})
