import React from 'react'
import { useParams,useLocation, Link } from 'react-router-dom'
import {useEffect} from 'react'
import { useNavigate } from 'react-router-dom'
import axios from 'axios'
import { useDispatch,useSelector } from 'react-redux'
import { cartAddItem,cartRemoveItem } from '../features/cartSlice'
import { Row,Col, ListGroup, Image ,Form, Button, Card} from 'react-bootstrap'
import Message from '../components/Message'
import { BASE_URL } from '../constants'

function CartScreen() {
    const {id} = useParams()
    const dispatch = useDispatch()
    const location = useLocation()
    const cartItems = useSelector((state) => state.cart.cartItems)
    const navigate = useNavigate()



    useEffect(()=>{
        fetchData()
    },[])
    const fetchData = async () => {
        try{
            // const response = await axios.get(`/getProduct/${id}`)
            const response = await axios.get(`${BASE_URL}getProduct/${id}`)  
            const data = response.data
            
            const searchParams = new URLSearchParams(location.search);
            const qty = parseInt(searchParams.get('qty')) || 1; 
            if(data){
                dispatch(cartAddItem({
                    product : data._id,
                    name : data.name,
                    image : data.image,
                    countInStock : data.countInStock,
                    price : data.price,
                    qty: qty

                }))
            }
            // localStorage.setItem('cartItems',JSON.stringify(useSelector((state)=>state.cartItems)))
        }
        catch(error){
            console.log(error)
        }
    } 


    const removefromCartHandler = (id) =>{

        dispatch(cartRemoveItem({
            product : id
        }))

    }

    const checkOutHandler = () => {
        navigate('/shipping')
    }




  return (
    <Row>
        <Col md={8}>
            <h1>Shopping Cart</h1>
            {cartItems.length === 0 ? (
                <Message variant="info">
                    Your cart is Empty<Link to='/' className='btn btn-block'> Go Back</Link>
                </Message>

            ) : (
                <ListGroup variant='flush'>
                    {cartItems.map((item) => (
                        <ListGroup.Item key={item.product}>
                            <Row>
                                
                                <Col md={2}>
                                    <Image src={item.image} alt={item.name} fluid rounded ></Image>
                                </Col>
                                <Col md={3}>
                                    <Link to={`/product/${item.product}`}>{item.name}</Link>
                                </Col>
                                <Col md={2}>
                                    ${item.price}
                                </Col>
                                <Col md={3}>
                                    <Form.Control as='select'  value={item.qty} onChange={(e) => {dispatch(cartAddItem({
                                         product : item.product,
                                         name : item.name,
                                         image : item.image,
                                         countInStock : item.countInStock,
                                         price : item.price,
                                         qty : Number(e.target.value)
                                    }))}}>
                                        {[...Array(item.countInStock).keys()].map((item) => (
                                                <option key={item + 1} value={item + 1}>{item + 1}</option>
                                            ))}
                                    </Form.Control>
                                </Col>
                                <Col md={1}>
                                    <Button type='button' variant='light'  onClick={() => removefromCartHandler(item.product)}>
                                        <i className='fas fa-trash'></i>
                                    </Button>
                                </Col>

                            </Row>
                        </ListGroup.Item>
                    ))}
                </ListGroup>
            )}
        </Col>
        <Col md={4}>
            <Card>
                <ListGroup variant='flush'>
                    <ListGroup.Item>
                        <h2>Subtotal({cartItems.reduce((acc,item)=> acc + item.qty,0)})</h2>
                        ${cartItems.reduce((acc,item)=> acc + item.qty*item.price,0)}
                    </ListGroup.Item>
                    <ListGroup.Item>
                        <Button type='button' className='btn btn-block w-100' disabled={cartItems.length === 0} onClick={checkOutHandler}>Checkout</Button>
                    </ListGroup.Item>
                </ListGroup>
            </Card>
        </Col>
    </Row>
  )
}

export default CartScreen
