
import { createSlice } from "@reduxjs/toolkit";

const initialState = {
    id: null,
    orderItems: [],
    shippingAddress: {},
    paymentMethod: '',
    itemsPrice: null,
    shippingPrice: null,
    taxPrice: null,
    totalPrice: null,
    success: false,
    error: false,
    errMsg: ''
};

const orderSlice = createSlice({
    name: 'order',
    initialState: initialState,
    reducers: {
        orderSuccess: (state, action) => {
            const {
                id ,
                orderItems,
                shippingAddress,
                paymentMethod,
                itemsPrice,
                shippingPrice,
                taxPrice,
                totalPrice
            } = action.payload;

            return {
                ...state,
                id ,
                orderItems,
                shippingAddress,
                paymentMethod,
                itemsPrice,
                shippingPrice,
                taxPrice,
                totalPrice,
                success: true,
                error: false
            };
        },
        orderFailure: (state, action) => {
            state.success = false;
            state.error = true;
            state.errMsg = action.payload.errMsg;
        },
        orderReset: (state, action) => {
            return initialState;
        }
    }
});

export const { orderSuccess, orderFailure, orderReset } = orderSlice.actions;
export default orderSlice.reducer;