import React, { useEffect, useReducer, useState } from 'react';
import { Link, useParams } from 'react-router-dom';
import { Row, Col, Card, Image, ListGroup, Button, Form } from 'react-bootstrap';
import Rating from '../components/Rating';
import Loader from '../components/Loader';
import Message from '../components/Message';
import { useSelector, useDispatch } from 'react-redux';
import axios from 'axios';
import { getProduct, getProductError } from '../features/productDetailSlice';
import { useNavigate } from 'react-router-dom';
import { BASE_URL } from '../constants';


const reducer = (state, action) => {
  switch (action?.type) {
    case "loading":
      return { ...state, loading: action?.data?.loading };
    case "success":
      return { ...state, success: action?.data?.success };
    case "error":
      return { ...state, error: action?.data?.error };
    case "reset":
      return { loading: false, success: false, error: false }; // Reset to default state
    default:
      return state;
  }
};

function ProductScreen() {
  const { id } = useParams();
  const navigate = useNavigate();
  
  
  const product = useSelector((state) => state.productDetails.product);
  const error = useSelector((state) => state.productDetails.error);
  const loading = useSelector((state) => state.productDetails.isLoading);
  const [status,dispatchStatus] = useReducer(reducer,{loading : false,success:false,error : false})

  const userLogin = useSelector((state) => state.user)
  const {userInfo} = userLogin
  const [qty, setQty] = useState(1);
  const [rating,setRating] = useState(0)
  const [comment,setComment] = useState("")
  const dispatch = useDispatch();

  useEffect(() => {
    async function fetchProduct() {
      try {
        const response = await axios.get(`${BASE_URL}/getProduct/${id}`);
        // const response = await axios.get(`/getProduct/${id}`);
        dispatch(getProduct({
          product: response.data,
          isLoading: false
        }));
      } catch (error) {
        console.log(error);
        dispatch(getProductError({
          error: error.message,
          isLoading: false
        }));
      }
    }
    fetchProduct();
    if (status?.success || status?.error) {
      const timer = setTimeout(() => {
        dispatchStatus({ type: 'reset' });
      }, 4000);
      
      return () => clearTimeout(timer); // Cleanup the timeout on component unmount or status change
    }
  }, [dispatch, id,status]);

  const addtocartHandler = () => {
   navigate(`/cart/${id}?qty=${qty}`)
  };


  const submitReview = async (e) => {
    e.preventDefault();
    try {
      dispatchStatus({
        type: 'loading',
        data: { loading: true }
      });
  
      const headers = new Headers();
      headers.append('Content-Type', 'application/json');
      headers.append('Authorization', `${userInfo?.token}`);
  
      const body = JSON.stringify({
        rating: rating,
        comment: comment
      });
  
      const response = await fetch(`http://127.0.0.1:8000/apis/review/${product?._id}`, {
        method: 'POST',
        headers: headers,
        body: body
      });
  
      if (!response.ok) {
        const error = await response.json();
        dispatchStatus({
          type: 'loading',
          data: { loading: false }
        });
        dispatchStatus({
          type: 'error',
          data: { error: true }
        });
      } else {
        const responseJson = await response.json();
        dispatchStatus({
          type: 'loading',
          data: { loading: false }
        });
        dispatchStatus({
          type: 'success',
          data: { success: true }
        });
      }
    } catch (error) {
      console.log(error, 'error');
      dispatchStatus({
        type: 'loading',
        data: { loading: false }
      });
      dispatchStatus({
        type: 'error',
        data: { error: true }
      });
    }
  };


  useEffect(() => {
   
  }, [status]);

  

  return (
    <div>
      <Link to='/' className='btn btn-light py-3 my-3'>Go Back</Link>
      {status?.success === true && (<Message variant="success" >Succesfully Submitted</Message>) }
      {status?.error === true && (<Message variant="danger">Failed to Submit Review</Message>)}
      {loading ? (
        <Loader />
      ) : error ? (
        <Message variant='danger'>{error}</Message>
      ) : (
        <>
         <Row>
          <Col md={6}>
            <Image src={product.image} alt={product.name} fluid />
          </Col>
          <Col md={3}>
            <ListGroup variant='flush'>
              <ListGroup.Item>
                <h3>{product.name}</h3>
              </ListGroup.Item>
              <ListGroup.Item>
                <Rating value={product.rating} text={`${product.numsReviews} reviews`} color={'#f8e825'} />
              </ListGroup.Item>
              <ListGroup.Item>
                Description: {product.description}
              </ListGroup.Item>
            </ListGroup>
          </Col>
          <Col md={3}>
            <Card>
              <ListGroup variant='flush'>
                <ListGroup.Item>
                  <Row>
                    <Col>Price:</Col>
                    <Col>
                      <strong>{product.price}</strong>
                    </Col>
                  </Row>
                </ListGroup.Item>
                <ListGroup.Item>
                  <Row>
                    <Col>Status:</Col>
                    <Col>
                      <strong>{product.countInStock > 0 ? 'In Stock' : 'Out of Stock'}</strong>
                    </Col>
                  </Row>
                </ListGroup.Item>
                {product.countInStock > 0 && (
                  <ListGroup.Item>
                    <Row>
                      <Col className='justify-content-center'>Quantity:</Col>
                      <Col >
                        <Form.Control
                          as="select"
                          value={qty}
                          onChange={(e) => setQty(e.target.value)}
                          className='text-left'
                        >
                          {[...Array(product.countInStock).keys()].map((item) => (
                            <option key={item + 1} value={item + 1}>{item + 1}</option>
                          ))}
                        </Form.Control>
                      </Col>
                    </Row>
                  </ListGroup.Item>
                )}
                <ListGroup.Item>
                  <Button
                    onClick={addtocartHandler}
                    className='btn-block w-100'
                    type='button'
                    disabled={product.countInStock === 0}
                  >
                    Add to Cart
                  </Button>
                </ListGroup.Item>
              </ListGroup>
            </Card>
          </Col>
        </Row>
        {/* <Row>
          <Col md={6}>
          <h4>Reviews</h4>
          {product?.reviews?.length === 0 && (<Message variant="info">No reviews found</Message>)}
          <ListGroup variant="flush">
          {product?.reviews?.map((review) => (

            <ListGroup.Item key={review?._id}>
              <strong>{review?.name}</strong>
              <Rating value={review?.rating} color="#f8e825" />
              <p>{review?.createdAt.substring(0,10)}</p>
              <p>{review?.message}</p>
            </ListGroup.Item>
          ))}
          <ListGroup.Item>
            <h4>Write a review</h4>
            {userInfo ? <Form>

            </Form> : <Message variant="info">Please <Link to="/login">Login</Link> to write a review</Message>}
          </ListGroup.Item>

          </ListGroup>
          
          </Col>  
        </Row> */}
        <Row>
  <Col md={6}>
    <h4>Reviews</h4>
    {product?.reviews?.length === 0 && (
      <Message variant="info">No reviews found</Message>
    )}
    <ListGroup variant="flush">
      {product?.reviews?.map((review) => (
        <ListGroup.Item key={review?._id}>
          <strong>{review?.name}</strong>
          <Rating value={review?.rating} color="#f8e825" />
          <p>{review?.createdAt.substring(0, 10)}</p>
          <p>{review?.comment}</p>
        </ListGroup.Item>
      ))}
      
      <ListGroup.Item>
        <h4>Write a review</h4>
        {userInfo ? (
          <Form onSubmit={submitReview}>
           <Form.Group controlId='rating' className="my-3">
            <Form.Label>Rating</Form.Label>
            <Form.Control
            as="select"
            name="rating"
            value={rating}
            onChange={(e) => setRating(e.target.value)}>
              <option value="">Select...</option>
              <option value="1">1</option>
              <option value="2">2</option>
              <option value="3">3</option>
              <option value="4">4</option>
              <option value="5">5</option>
            </Form.Control>
           </Form.Group>
           <Form.Group controlId='comment' className='my-3'>
            <Form.Label>Review</Form.Label>
            <Form.Control
            as="textarea"
            name="comment"
            row="5"
            value={comment}
            onChange={(e) => setComment(e.target.value)}>
            
            </Form.Control >
           </Form.Group >
           <Form.Group controlId='formSubmit' className='my-3'>
            <Button variant='primary' type="submit" disabled={status?.loading}>Submit</Button>
           </Form.Group>
          </Form>
        ) : (
          <Message variant="info">
            Please <Link to="/login">Login</Link> to write a review
          </Message>
        )}
      </ListGroup.Item>
    </ListGroup>
  </Col>
</Row>
        </>
       
      )}
    </div>
  );
}

export default ProductScreen;
