import React,{useEffect} from 'react'
// import { Link,useNavigate, useLocation } from 'react-router-dom';
import { Button, Table } from 'react-bootstrap'
import Loader from '../components/Loader';
import Message from '../components/Message';
import { useDispatch, useSelector } from 'react-redux';
import { LinkContainer } from 'react-router-bootstrap';
import { orderListRequest,orderListSuccess,orderListFailure } from '../features/orderListSlice';
import {  useNavigate } from 'react-router-dom';

function OrderListScreen() {

    const dispatch = useDispatch()
    const navigate = useNavigate()
    const userInfo = useSelector((state)=>state.user.userInfo)
    const orderList = useSelector((state)=>state.orderList)
    const {loading,orders = [],err,errMsg} = orderList

    const getOrderList = async() => {
        dispatch(orderListRequest())
        try{
            const headers = new Headers({
                'Content-Type' : 'application/json',
                Authorization : `Bearer ${userInfo.token}`
            })
            const response = await fetch('http://127.0.0.1:8000/apis/admin/getAllOrders/',{
                method : 'GET',
                headers
            })
            if(!response.ok){
                const error = await response.json()
                return new Error(error)
            }
            const responseJson = await response.json()
            if(responseJson?.msg === "Success"){
                dispatch(orderListSuccess(responseJson?.data))
            
            }else{
                dispatch(orderListFailure(responseJson?.error))
            }   
        }
        catch(error){
            console.log(error)
            dispatch(orderListFailure(error))
        }
    }


    

    useEffect(()=>{
        if(!userInfo || !userInfo.name || !userInfo.name === ""){
            navigate('/')
            return
        }
        getOrderList()
    },[userInfo])
  return (
    <div>
        <h1>Orders</h1>
        {loading ? (<Loader/>) : 
                err ? (<Message variant='danger'>{errMsg!=="" ? errMsg : "Error Occurred"}</Message>)
                    : (
                        <Table striped responsive bordered hover className='table-sm'>
                            <thead>
                                <th>ID</th>
                                <th>USER</th>
                                <th>DATE</th>
                                <th>PRICE</th>
                                <th>PAID</th>
                                <th>DELIVERED</th>
                                <th></th>
                            </thead>
                            <tbody>
                                {orders.map((order)=> (
                                    <tr key={order._id}>
                                        <td >{order._id}</td>
                                        <td >{order.user && order.user.name}</td>
                                        <td >{order.createdAt.substring(0,10)}</td>
                                        <td >{order.totalPrice}</td>
                                        <td >{order.isPaid ? (
                                            order.PaidAt?.substring(0,10)
                                            
                                        ): (
                                            <i className='fas fa-check' style={{color : 'red'}}></i>
                                        )}</td>
                                         <td >{order.isDelivered ? (
                                            order.deliveredAt?.substring(0,10)
                                        ): (
                                            <i className='fas fa-check' style={{color : 'red'}}></i>
                                        )}</td>
                                        <td >
                                            <LinkContainer to={`/order/${order._id}`}>
                                                <Button variant='light'>
                                                    Details
                                                </Button>
                                            </LinkContainer>
                                
                                        </td>
                                        
                                    </tr>
                                ))}
                                
                            </tbody>

                        </Table>
                    )}
      
    </div>
  )
}

export default OrderListScreen
